section.screen_kit {
    position: fixed;
    width: 100vw;
    height: 100vh;
    color: red;
    font-size: 40px;
    z-index: 200;
    pointer-events: none;

    .wrapper {
        position: relative;
        padding: calc(10px + 40/1920*100vw);
        width: 100%;
        height: 100%;

        &>* {
            pointer-events: all;
        }

        .button {
            position: absolute;
            white-space: nowrap;
            bottom: 10px;
            padding: 10px 20px;
            transform-origin: 0 0;
            --color: var(--bodyColorContrast);
            --colorHover: var(--bodyColorSuper);
            transform: rotateZ(-90deg) translateX(20px);
            transition: 0.4s ease-in-out;
            opacity: 0;

            &.show {
                transform: rotateZ(-90deg);
                opacity: 1;

                @media (max-width:720px) {
                    transform: rotateZ(-90deg) translateX(50px);
                    opacity: 0;
                }
            }
        }

        .helper {
            position: absolute;
            white-space: nowrap;
            bottom: -40px;
            left: 260px;
            transform-origin: 0 0;
            --color: var(--bodyColorContrast);
            --colorHover: var(--bodyColorSuper);
            transform: rotateZ(-90deg) translateX(20px);
            transition: 0.4s ease-in-out;
            font-size: 14px;
            text-transform: uppercase;
            font-weight: 700;
            letter-spacing: .3em;
            color: white;

            &::before {
                content: "";
                margin-right: 5px;
                display: inline-block;
                width: 60px;
                height: 2px;
                // background-color: white;
                background-image: linear-gradient(to right, white, white);
                animation: screen_kit__lineAnim 4000ms ease;
                background-repeat: no-repeat;
                vertical-align: middle;
                animation-iteration-count: infinite;

                @keyframes screen_kit__lineAnim {
                    from {
                        background-size: 100% 100%;
                        background-position: left;
                    }

                    15% {
                        background-size: 0% 100%;
                        background-position: left;
                    }

                    16% {
                        background-size: 0% 100%;
                        background-position: right;
                    }

                    40% {
                        background-size: 100% 100%;
                        background-position: right;
                    }
                }
            }
        }

        .contacts {
            position: absolute;
            right: 50px;
            bottom: 50px;
            display: flex;
            flex-direction: column;
            width: max-content;
            font-size: 16px;
            color: var(--bodyColorContrast);
            transition: 0.5s ease-in;

            @media (max-width:720px) {
                transform: translateY(-50px);
                opacity: 0;
            }

            :not(:first-child) {
                margin-top: 15px;
            }

            .number {
                transform: rotate(180deg);
                writing-mode: vertical-lr;
            }

            svg {
                width: 20px;
                fill: var(--bodyColorContrast);
            }

        }
    }
}