
body {
    //=====Цвет задавать только переменной!!!===
    --bodyColor:#1a1a1a;
    --bodyColorSuper:black;
    --bodyColorContrast:white;
    --logoColor: white;
    background-color: var(--bodyColor);
    // background-color: #999;
    transition: 0.3s ease-in;

    &.body__bgc_white{
        --bodyColor: #efeded;
        --bodyColorSuper: white;
        --bodyColorContrast: black;
        --logoColor: #E31E24;
    }
}

body,
button {
    color: black;
    font: 400 16px Montserrat, sans-serif;
    line-height: normal;
}

.body-wrapper {
    position: relative;
    min-height: 200%;
    // overflow: hidden;
}

._conteiner {
    margin: 0 auto;
    padding: 0 5px;
    width: calc(85px + 1180/1920*100vw);
    max-width: 1260px;
    box-sizing: border-box;
}
._conteiner-big {
    margin: 0 auto;
    // padding: 0 10px;
}

