section.why_we {
    margin-top: calc(100px + 100/1920*100vw);
    color: white;

    .title {
        font-size: 24px;
        font-weight: 700;
        text-transform: uppercase;
    }

    ul {
        margin-top: calc(50px + 50/1920*100vw);
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-column-gap: 5%;
        grid-row-gap: 10%;

        font-weight: 500;
        line-height: 150%;

        @media(max-width:1000px){
            grid-template-columns: 1fr 1fr ;
        }
        @media(max-width:700px){
            font-size: 14px;
        }
        li{
            p{
                margin-top: calc(25px + 25/1920*100vw);
            }
        }
    }
}