header {
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: calc(10px + 15/1920*100vw) calc(10px + 20/1920*100vw) calc(8px + 15/1920*100vw);
    // background-color: #999;
    z-index: 10;

    .wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
        z-index: 10;

        &.wrapper_left {
            .logo {
                opacity: 0;
                animation: header__show_logo 0.4s ease-in forwards;
                animation-delay: 0.5s;

                @keyframes header__show_logo {
                    from {
                        transform: translateX(-20px);
                    }

                    80% {
                        opacity: 1;
                    }

                    to {
                        transform: translateX(0);
                        opacity: 1;
                    }
                }

                svg {
                    width: calc(40px + 45/1920*100vw);

                    path {
                        fill: var(--logoColor);
                    }
                }
            }

            .button {
                margin-left: calc(20px + 80/1920*100vw);
                transition: 0.4s ease-in 0.2s;
                --color: var(--bodyColorContrast);
                --colorHover: var(--bodyColorSuper);

                &.animFirstShow {
                    opacity: 0;
                    animation: header__show_buttom 0.4s ease-in forwards;
                    animation-delay: 0.8s;

                    @keyframes header__show_buttom {
                        from {
                            transform: translateX(-20px);
                        }

                        70% {
                            opacity: 1;
                        }

                        to {
                            transform: translateX(0);
                            opacity: 1;
                        }
                    }
                }

                .short_title {
                    display: none;
                }

                @media (max-width:1200px) {
                    font-size: 14px;
                }

                @media (max-width:800px) {
                    margin-left: calc(15px + 10/1920*100vw);

                    .full_title {
                        display: none;
                    }

                    .short_title {
                        display: block;
                    }
                }

            }
        }

        &.wrapper_right {
            display: flex;
            justify-content: space-between;
            text-transform: uppercase;

            nav {
                transition: 0.5s ease-in;
            }

            ul {
                display: flex;
                justify-content: space-between;

                &.short_nuv {
                    display: none;

                    svg {
                        width: 20px;
                        fill: var(--bodyColorContrast);
                    }

                    @media (max-width:370px) {
                        svg {
                            width: 15px;
                        }

                        li {
                            margin-right: calc(10px + 10/1920*100vw);
                        }

                        li:last-child {
                            margin-right: calc(15px);
                        }
                    }

                }

                li {
                    margin-right: calc(20px + 10/1920*100vw);
                    transition: 0.5s ease-in;
                    transition-delay: calc(var(--i)*50ms);

                    &.animFirstShow {
                        opacity: 0;
                        animation: header__show_listElem 0.4s ease-in forwards;
                        animation-delay: calc(var(--i)*250ms + 1.5s);


                        @keyframes header__show_listElem {
                            from {

                                transform: translateX(-20px);
                            }

                            70% {
                                opacity: 1;
                            }

                            to {
                                transform: translateX(0);
                                opacity: 1;

                            }
                        }
                    }

                }

                a {
                    // display: block;
                    color: white;
                    color: var(--bodyColorContrast);
                    font-weight: 700;
                    font-size: 14px;

                    &._template_underline_style_0 {
                        --color: var(--bodyColorContrast);
                    }
                }

                @media (max-width:720px) {
                    &.full_nav {
                        display: none;
                    }

                    &.short_nuv {
                        display: flex;
                    }
                }

                @media (max-width:330px) {
                    &.short_nuv {
                        display: none;
                    }
                }

            }

            .burger {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                width: calc(27px + 8/1920*100vw);
                height: calc(22px + 5/1920*100vw);
                // background-color: red;
                overflow: hidden;

                &.animFirstShow {
                    opacity: 0;
                    animation: header__show_burger 0.5s ease-in forwards;
                    animation-delay: 4.5s;

                    @keyframes header__show_burger {
                        from {
                            transform: translateX(0px) scale(2);
                        }

                        40% {
                            opacity: 0.8;
                        }

                        80% {
                            transform: translateX(0) scale(0.7);
                            opacity: 1;
                        }

                        to {
                            transform: translateX(0);
                            opacity: 1;
                        }
                    }
                }

                .line {
                    position: relative;
                    left: -130%;
                    height: 4px;
                    width: 230%;
                    border-radius: 1px;
                    background-color: transparent;
                    background-image: linear-gradient(90deg, var(--bodyColorContrast) 48%, transparent 48%, transparent 56%, var(--bodyColorContrast) 56%);

                }

                &:hover {
                    cursor: pointer;

                    @media (min-width:720px) {
                        .line {
                            animation: animate_burgerLine 0.8s ease-out infinite;
                            animation-delay: calc(var(--i)*110ms);
                        }
                    }
                }
            }
        }
    }

    &.hidden_nav {
        @media (min-width:721px) {

            li {
                opacity: 0 !important;
                pointer-events: none;
                transform: translateX(20px) !important;
            }

            .button {
                opacity: 0;
                pointer-events: none;
                transform: translateX(20px);
                transition-delay: 0.1s !important;
            }
        }
    }

    .background {
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: var(--bodyColorSuper);
        filter: opacity(80%);
        z-index: 2;

        @media (max-width:720px) {
            display: block;
        }
    }
}

@keyframes animate_burgerLine {

    0% {
        left: -130%;
    }

    80%,
    100% {
        left: 0%;
    }
}