section.blabla {
    margin-top: 88px;
    color: white;

    .showAnim{
        transition: 0.5s ease-in;
    }
    .waitingElement {
        transform: translateY(30px);
        opacity: 0;
    }

    h3 {
        width: calc(150px + 550/1920*100vw);
        max-width: 700px;
        line-height: 1.7;

        @media (max-width:600px) {
            width: auto;
            font-size: 16px;

        }

        @media (max-width:400px) {
            font-size: 14px;
        }
    }
    
}