section.form {
    margin-top: calc(100px + 100/1920*100vw);
    color: white;

    .block_title {
        font-size: 30px;

    }

    .wrapper {
        margin-top: calc(20px + 40/1920*100vw);
        display: flex;
        justify-content: space-between;

        @media (max-width:1200px) {
            display: block;
        }

        form {
            flex-shrink: 1;

            .form_wrapper {
                @media (max-width:650px) {
                    flex-direction: column;

                    &>* {
                        margin-left: 0 !important;
                    }
                }
            }

            .input {
                position: relative;
                display: block;
                margin-top: 50px;
                width: 100%;

                @media (max-width:650px) {
                    margin-top: 70px;
                }

                &:not(:first-child):not(.message_input) {
                    margin-left: 20px;
                }

                input,
                textarea {
                    position: relative;
                    top: -7px;
                    // width: 100%;
                    background-color: transparent;
                    // background-color: rgba(0, 202, 0, 0.426);
                    border: 0;
                    color: white;
                    font-size: 16px;

                    &:focus,
                    &:active,
                    &:valid {
                        outline: 0;

                        &~.line__box .line {
                            transform: translateX(0%);
                            transition: 0.2s ease-in;
                        }

                        &+i {
                            top: -35px;
                            font-size: 14px;
                        }

                        &~i.telSymbol {
                            opacity: 1;
                        }

                    }

                    &#message {
                        width: 100%;
                    }
                }

                &.tel_input {
                    box-sizing: border-box;

                    i.telSymbol {
                        position: absolute;
                        left: 0;
                        top: -7px;
                        opacity: 0;
                    }

                    input {
                        margin-left: 10px;
                        width: 90%;

                    }
                }

                i.placeholder {
                    position: absolute;
                    left: 0px;
                    top: -7px;
                    font-size: 16px;
                    pointer-events: none;
                    transition: 0.2s ease-in;
                    opacity: 0.5;
                }

                .line__box {
                    position: absolute;
                    top: 0;
                    left: 0;
                    overflow: hidden;
                    height: 100%;
                    width: 100%;
                    pointer-events: none;

                    .line {
                        position: absolute;
                        bottom: 0;
                        height: 1px;
                        width: 200%;
                        background-image: linear-gradient(to left,
                                rgba(255, 255, 255, .3) 50%, white 50%);
                        transform: translateX(-50%);
                        transition: 0.3s ease-in;
                    }
                }

                textarea,
                .textareaSizeMetter {
                    padding: 0;
                    // background-color: rgba(0, 128, 0, 0.462);
                    overflow: visible;
                    font-size: 16px;
                    font-family: Montserrat;
                    overflow-wrap: break-word;
                    overflow: hidden;
                    min-height: 20px;

                }

                textarea {
                    height: 20px;
                    resize: none;
                }

                .textareaSizeMetter {
                    position: absolute;
                    width: 100%;
                    opacity: 0;
                    pointer-events: none;
                    z-index: -100;
                }
            }

            .submit {
                margin-top: 50px;

                input {
                    padding: 10px 20px;
                    font-size: 14px;
                    font-weight: inherit;
                    text-transform: uppercase;
                    background-color: transparent;
                    color: inherit;
                    border: 0;
                    ;
                }
            }

            .form_wrapper {
                display: flex;
            }
        }

        .our_contacts {
            margin-left: 20px;
            width: 300px;
            font-size: 14px;
            font-weight: 300;
            line-height: 1.7;
            color: #868686;
            flex-shrink: 0;

            @media (max-width:1200px) {
                display: flex;
                margin-top: 50px;
                width: 100%;
                margin-left: 0px;
            }

            @media (max-width:650px) {
                flex-direction: column;
            }

            &>* {

                &:not(:first-child) {
                    margin-top: 30px;

                    @media (max-width:1200px) {
                        margin-top: 0px;
                        margin-left: 20px;
                    }

                    @media (max-width:650px) {
                        margin-top: 30px;
                        margin-left: 0px;
                    }

                }

                @media (max-width:1200px) {
                    width: 100%;
                }

            }

            a {
                margin-top: 15px;
                color: white;
            }

            .messages {
                display: inline-block;
                margin-left: 20px;

                path {
                    fill: white;
                }

                &>* {
                    margin-left: 15px;
                }
            }
        }
    }
}