section.our_calling {
    margin-top: calc(100px + 100/1920*100vw);
    padding-top: calc(100px + 100/1920*100vw);
    // background-color: white;

    .grid {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-column-gap: 5%;
        grid-row-gap: 70px;

        @media(max-width:1200px) {
            grid-template-columns: 1fr 1fr;
        }

        @media(max-width:800px) {
            grid-template-columns: 1fr;
        }

        .title {
            font-size: 28px;
            line-height: 1.5;
            font-weight: 700;
            text-transform: uppercase;
            letter-spacing: .05em;

            @media(max-width:1200px) {
                font-size: 28px;
            }

            @media(max-width:800px) {
                max-width: 500px;
                text-align: center;
                font-size: 26px;

                br {
                    display: none;
                }
            }

            @media(max-width:400px) {
                font-size: 20px;
            }
        }

        .card {
            cursor: pointer;

            .wrapper {
                display: flex;
                align-items: center;
                justify-content: space-between;

                .price {
                    font-size: 18px;
                    color: rgba(0, 0, 0, .4);

                    .number {
                        font-size: 24px;
                    }
                }
            }

            .card__title {
                margin: 20px 0;
                font-size: 18px;
                font-weight: 700;
                white-space: nowrap;

                &._template_underline_style_0__target::after {
                    background-color: black;
                }
            }

            .card__description {
                font-size: 14px;
                line-height: 1.5;
            }
        }
    }

    .view_services {
        display: block;
        margin: calc(80px + 60/1920*100vw) auto 20px;
        text-align: center;
        font-size: calc(15px + 25/1920*100vw);

        @media (min-width: 1920px) {
            font-size: 40px;
        }

        ._template_underline_style_1 {
            --line-color:black;
            --line-height: 2px;
        }
    }
}